<template>
	<view class="main">
		<view class="accountBox">
			<input type="text" v-model="info.name" placeholder="用户昵称" focus class="account"/>
			<input type="text" v-model="info.account_num" placeholder="请输入QQ号码作为登陆账号" class="account"/>
			<input type="number" v-model="info.phone" maxlength="13" placeholder="请输入手机号码用于找回密码" class="account"/>
			<input type="text" v-model="info.password" placeholder="请输入密码" class="pwd"/>
			<input type="text" v-model="pwd" 
			placeholder="请再次输入密码" class="pwd" @blur="proofread"/>
			<input type="text" v-model="info.invite_code" placeholder="邀请码(非必填)" class="account"/>
		</view>
		<button type="default" class="btn" @click="registered">注册</button>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				//注册的用户信息
				info:{
					name:'',
					account_num:"",
					phone:"",
					password:"",
					invite_code:"",
				},
				pwd:""
			}
		},
		methods: {
			//两次不一致密码没改就不让注册
			async registered(check){
				const res = await this.$myRequest({
					url:'/registers',
					data:this.info,
				})
				if(res.data.code === 1){
					uni.showToast({
						title:'注册成功'
					})
					uni.redirectTo({
						url:'/pages/login/login'
					})
				}
			},
			//校对两次密码
			proofread(){
				if(this.info.password){
					if(this.info.password !== this.pwd){
						uni.showToast({
							title:'两次密码不一致'
						})
						this.registered(1)
					}					
				}
			}
		},
		computed:{
			
		}
	}
</script>

<style lang="scss">
	.main{
		display: flex;
		flex-direction: column;
		align-items: center;
		.accountBox{
			display: flex;
			flex-direction: column;
			margin-top: 70rpx;
			input{
				width: 558rpx;
				height: 80rpx;
				border: 4rpx solid #f2f2f2;
				border-radius: 8rpx;
				font-size: $font-size-12;
				padding-left: 76rpx;
				box-sizing: border-box;
				opacity: .8;
				margin: 10rpx 0;
			}
			.account{
				background: url('data:image/svg+xml;base64,') no-repeat 16rpx center;
				background-size: 50rpx 50rpx;
			}
			.pwd{
				background: url(data:image/svg+xml;base64,) no-repeat 16rpx center;
				background-size: 50rpx 50rpx;
			}
		}
		.btn{
			width: 558rpx;
			height: 90rpx;
			background-color: $green-bg-color;
			color: white;
			margin-top: 80rpx;
		}
	}
</style>
